.banner {
    background-image:linear-gradient(to right, red , yellow);
    height: 100vmin;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
    z-index: 10;
    
}

.banner>h1 {
    margin: 5vmax;
    font: 600 2.5vmax "Roboto";
}

.banner>p {
    font: 300 1.4max "Lucida Sans";
}

.banner>a{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.banner>a>button {
    margin-bottom: 5vmax;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid #fff;
    border-radius: 0;
    padding: 1vmax;
    transition: all 0.5s;
    width: 9vmax;
    font: 500 1.2vmax "Roboto";

}

.banner > a > button:hover{
    background-color: rgba(225, 225, 225, 0);
    color: #fff;
    
}

.banner::after{
    content: "";
    width: 100vw;
    height: 100vmin;
    background-color: #ffffff;
    position: absolute;
    top: 0%;
    max-width: 100%;
    clip-path: polygon(75% 0, 100% 0, 100% 50%, 100% 100%, 75% 100%, 100% 50%);
  
}
